<template>
  <div class="search">
    <Header></Header>
    <section class="search-history">
      <div class="s-history">
        <h2>
          <span><i class="iconfont icon-lishijilu"></i>历史搜索</span>
          <span
            style="color: rgb(150, 150, 150); font-size: 0.3rem"
            @click="clearSearchList"
            >清空搜索记录</span
          >
        </h2>
        <ul>
          <li
            v-for="(item, index) in searchList"
            :key="index"
            @click="goSearchList(item)"
          >
            {{ item }}
          </li>
        </ul>
        <span
          v-if="isNoSearch"
          style="
            color: rgb(150, 150, 150);
            font-size: 0.4rem;
            margin-bottom: 0.5rem;
            padding: 0 0.5rem;
          "
          >暂无搜索记录</span
        >
      </div>
      <div class="s-hot">
        <h2>
          <span> <i class="iconfont icon-hot"></i> 热门搜索</span>
        </h2>
        <ul>
          <li>紫砂壶</li>
          <li>铁观音</li>
          <li>精品毛尖</li>
          <li>茉莉花茶</li>
          <li>茉莉绿茶</li>
          <li>茶具套装</li>
          <li>花茶</li>
          <li>送礼茶</li>
          <li>自己喝的茶</li>
          <li>高端茶叶</li>
        </ul>
      </div>
    </section>
    <Tabber></Tabber>
  </div>
</template>

<script>
import Header from "@/components/Search/Header.vue";
import Tabber from "@/components/common/Tabber.vue";
import { MessageBox } from "mint-ui";
export default {
  name: "search-index",
  components: {
    Tabber,
    Header,
  },
  data() {
    return {
      searchList: [],
      isNoSearch: false,
    };
  },
  mounted() {
    this.getSearchList();
  },
  methods: {
    //清楚搜索历史的回调函数
    clearSearchList() {
      MessageBox.confirm("您确定执要删除?", "提示")
        .then(() => {
          localStorage.removeItem("searchList");
          this.searchList = [];
          this.getSearchList();
        })
        .catch(() => {});
    },
    //判断locatStorage中是否有数据，以及是否展示没有搜索记录
    getSearchList() {
      if (localStorage.getItem("searchList") != null) {
        this.isNoSearch = false;
        this.searchList = JSON.parse(localStorage.getItem("searchList"));
      } else {
        this.isNoSearch = true;
      }
    },

    //点击历史记录进行搜索
    goSearchList(item) {
      console.log(item);
      this.$router.push({
        name: "searchList",
        query: {
          key: item,
        },
      });
      item = "";
    },
  },
};
</script>

<style scoped>
.search {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
section {
  flex: 1;
  background-color: #f5f5f5;
  overflow: hidden;
}
.search-history h2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  font-weight: 500;
  font-size: 0.48rem;
}
.search-history .s-history h2 i {
  color: rgb(247, 131, 23);
}
.search-history .s-hot h2 i {
  color: rgb(232, 48, 48);
}
.search-history ul {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0.26rem;
  justify-content: space-around;
  align-content: center;
}
.search-history ul li {
  margin: 0.26rem;
  padding: 0.1rem 0.15rem;
  font-size: 0.38rem;
  font-weight: 400;
  border: 1px solid #ccc;
}
</style>
